"use client";
import React, { useState } from 'react';
import { DollarSign, Briefcase, Megaphone, Check, Shield } from 'lucide-react';
import '@/styles/pages/GetStarted.css';

interface PricingTier {
    id: string;
    name: string;
    description: string;
    monthlyPrice: number;
    oneTimePrice: number;
    icon: React.ReactNode;
}

const FINANCE_ADDON: PricingTier = {
    id: 'finance',
    name: 'Finance Division',
    description: 'Advanced fintech tools, yield vaults, and risk assessment protocols.',
    monthlyPrice: 149.00,
    oneTimePrice: 0,
    icon: <DollarSign size={32} />
};

const DEVELOPMENT_ADDON: PricingTier = {
    id: 'development',
    name: 'Development Division',
    description: 'Access to the Career Simulator, Genealogy Visualizer, and Growth Logic.',
    monthlyPrice: 75.00,
    oneTimePrice: 149.00,
    icon: <Megaphone size={32} />
};

export default function GetStartedPage() {
    const [addons, setAddons] = useState<string[]>([]);

    const toggleAddon = (id: string) => {
        if (addons.includes(id)) {
            setAddons(addons.filter(a => a !== id));
        } else {
            setAddons([...addons, id]);
        }
    };

    // Calculations
    const coreMonthly = 49.99;

    let totalMonthly = coreMonthly;
    let totaltoday = coreMonthly;

    if (addons.includes('finance')) {
        totalMonthly += FINANCE_ADDON.monthlyPrice;
        totaltoday += FINANCE_ADDON.monthlyPrice; // Assuming monthly paid upfront
    }

    if (addons.includes('development')) {
        totalMonthly += DEVELOPMENT_ADDON.monthlyPrice;
        totaltoday += DEVELOPMENT_ADDON.oneTimePrice + DEVELOPMENT_ADDON.monthlyPrice;
    }

    return (
        <div className="get-started-page animate-fade-in">
            <div className="gs-container">
                {/* Left Column: Selection */}
                <div className="selection-area">
                    <div className="gs-header">
                        <h1>Initialize Your Access</h1>
                        <p>Customize your Verstige Operating System configuration.</p>
                    </div>

                    {/* CORE MEMBERSHIP (Always Included) */}
                    <div className="core-plan-card">
                        <div className="core-info">
                            <h3>Core Membership <span className="badge-included">Required</span></h3>
                            <p className="text-secondary text-sm mb-2">Includes Business Division Access</p>
                            <ul className="text-sm text-secondary space-y-1">
                                <li className="flex items-center gap-2"><Check size={14} className="text-green-500" /> Central Dashboard</li>
                                <li className="flex items-center gap-2"><Check size={14} className="text-green-500" /> Private Community</li>
                                <li className="flex items-center gap-2"><Check size={14} className="text-green-500" /> Business Division Tools</li>
                            </ul>
                        </div>
                        <div className="core-price">
                            <span className="price-large">$49.99</span>
                            <span className="price-period">/ month</span>
                        </div>
                    </div>

                    <h3 className="text-lg font-bold mb-4 uppercase text-secondary tracking-wider">Available Expansions</h3>

                    <div className="addon-grid">
                        {/* FINANCE ADDON */}
                        <div
                            className={`addon-card ${addons.includes('finance') ? 'selected' : ''}`}
                            onClick={() => toggleAddon('finance')}
                        >
                            <div className="selection-indicator"></div>
                            <div className="addon-icon">{FINANCE_ADDON.icon}</div>
                            <div className="addon-content">
                                <h4>{FINANCE_ADDON.name}</h4>
                                <p className="addon-desc">{FINANCE_ADDON.description}</p>
                                <div className="addon-pricing">
                                    <span className="price-tag highlight">+$149.00 / mo</span>
                                </div>
                            </div>
                        </div>

                        {/* DEVELOPMENT ADDON */}
                        <div
                            className={`addon-card ${addons.includes('development') ? 'selected' : ''}`}
                            onClick={() => toggleAddon('development')}
                        >
                            <div className="selection-indicator"></div>
                            <div className="addon-icon">{DEVELOPMENT_ADDON.icon}</div>
                            <div className="addon-content">
                                <h4>{DEVELOPMENT_ADDON.name}</h4>
                                <p className="addon-desc">{DEVELOPMENT_ADDON.description}</p>
                                <div className="addon-pricing flex-col gap-0">
                                    <span className="price-tag highlight">+$75.00 / mo</span>
                                    <span className="text-xs text-secondary">+$149.00 One-time Activation</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                {/* Right Column: Summary */}
                <div className="order-summary">
                    <h3>Order Summary</h3>

                    {/* Core Line Item */}
                    <div className="summary-row">
                        <span>Core Membership</span>
                        <span>$49.99</span>
                    </div>
                    <div className="summary-row sub-item">
                        <span>Includes Business Div.</span>
                    </div>

                    {/* Finance Line Item */}
                    {addons.includes('finance') && (
                        <div className="summary-row animate-fade-in">
                            <span>Finance Add-on</span>
                            <span>$149.00</span>
                        </div>
                    )}

                    {/* Development Line Item */}
                    {addons.includes('development') && (
                        <>
                            <div className="summary-row animate-fade-in">
                                <span>Development Add-on</span>
                                <span>$75.00</span>
                            </div>
                            <div className="summary-row sub-item animate-fade-in">
                                <span>Dev. Activation Fee</span>
                                <span>$149.00</span>
                            </div>
                        </>
                    )}

                    <div className="summary-total">
                        <div className="total-row">
                            <span className="text-secondary">Due Today</span>
                            <span className="grand">${totaltoday.toFixed(2)}</span>
                        </div>
                        <div className="total-row">
                            <span className="text-secondary text-sm">Monthly Recurring</span>
                            <span className="font-mono text-white">${totalMonthly.toFixed(2)}</span>
                        </div>
                    </div>

                    <button className="btn-checkout">
                        Proceed to Payment
                    </button>
                    <p className="text-xs text-center mt-4 text-secondary">
                        Secure 256-bit automated encryption.
                    </p>
                </div>
            </div>
        </div>
    );
}
